@import url(https://fonts.googleapis.com/css2?family=Fira+Code:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fira+Code:wght@400;500;700&family=Montserrat:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: auto;
  width: auto;
  margin: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*,*::before,*::after{box-sizing:border-box}body,h1,h2,h3,h4,p,figure,blockquote,dl,dd{margin:0}ul[role="list"],ol[role="list"]{list-style:none}ul,ol{padding:0}html:focus-within{scroll-behavior:smooth}body{height:100vh;text-rendering:optimizeSpeed;line-height:1.5}a:not([class]){-webkit-text-decoration-skip:ink;text-decoration-skip-ink:auto}img,picture{max-width:100%;display:block}input,button,textarea,select{font:inherit}@media (prefers-reduced-motion: reduce){html:focus-within{scroll-behavior:auto}*,*::before,*::after{animation-duration:0.01ms !important;animation-iteration-count:1 !important;transition-duration:0.01ms !important;scroll-behavior:auto !important}}p,span,em{font-family:"Fira Code";font-size:1rem;font-weight:500}h1{font-size:2rem}h2{font-size:1.5rem}h3{font-size:1.25rem}h1,h2,h3{font-family:"Montserrat"}#root{margin:0;width:100vw;min-height:100vh;background-color:#282828;display:grid;grid-template-columns:2fr 10fr;grid-template-rows:100%}.nav-wrapper{display:flex;justify-content:space-between;flex-direction:column}.nav-wrapper h2,.nav-wrapper h3,.nav-wrapper p,.nav-wrapper a,.nav-wrapper pre,.nav-wrapper span{color:white}.nav-wrapper .title{display:flex;justify-content:flex-start;align-items:center;flex-direction:column;padding:20px 20px;color:#e6c973}.nav-wrapper .title p{font-weight:bold}.nav-wrapper .styleguide-nav{padding:10px 20px;display:flex;align-items:center;font-family:"Montserrat";flex-direction:column;list-style:none}.nav-wrapper .styleguide-nav li{list-style:none;margin-top:10px;margin-left:10px}.nav-wrapper .styleguide-nav li a{outline:none;text-decoration:none;font-family:"Fira Code";font-size:1.5rem;font-weight:500}.nav-wrapper .styleguide-nav li a.is-active-link{font-weight:bold;color:#e6c973}.nav-wrapper .authors{display:flex;justify-content:space-evenly;align-items:center}.page{color:white;overflow-y:scroll;padding-top:20px;padding-right:40%;max-height:100vh}.page section{margin-top:70px;border-left:3px solid #91a18d;padding-left:30px}.page section h1,.page section h2,.page section h3{text-transform:uppercase}.page section h1,.page section h2{color:#91a18d}.page section p{font-size:1.2rem;margin-top:20px;margin-bottom:20px}.page section:first-of-type{margin-top:0}.page section:last-of-type{margin-bottom:30px}.page section .Collapsible{padding:20px}.page section .Collapsible .syntax-wrapper{width:100%;display:flex;justify-content:center;align-items:center}.page section .Collapsible .syntax-wrapper pre{width:90%;padding:20px !important;background-color:rgba(155,155,155,0.1) !important;border-radius:10px}.page section .Collapsible .copy-field{position:relative}.page section .Collapsible img{transition:0.3s ease all;width:2rem;position:absolute;-webkit-filter:invert(1);filter:invert(1);opacity:0.5;right:3.5%}.page section .Collapsible img:hover{opacity:1;transform:scale(1.1);cursor:pointer}.page section .Collapsible span{transition:0.3s ease all;color:#e6c973}.page section .Collapsible span:hover{color:white;cursor:pointer}.module{height:auto;border-radius:20px;padding:20px;justify-self:center;width:80%;background-color:#9b9b9b;display:flex;justify-content:space-around;align-items:space-around;margin-left:auto;margin-right:auto;margin-top:40px;margin-bottom:40px;flex-wrap:wrap}.module h1,.module h2,.module h3{text-transform:none !important;color:black !important}

.color-module{grid-row-gap:30px;row-gap:30px}.color-module div{padding:20px;border:3px solid black;border-radius:10px;font-size:1.4rem;display:flex;flex-basis:40%;justify-content:center;align-items:center;flex-direction:column;transition:0.5s ease all}.color-module div:hover{flex-basis:50%}.color-module .color1{background-color:#282828}.color-module .color2{background-color:#c4c4c4;color:black}.color-module .color3{background-color:#e6c973;color:black}.color-module .color4{background-color:#91a18d;color:black}.color-module .color5{background-color:#9b9b9b;color:black}

.fonts-module{color:black;font-size:2rem}.fonts-module .fira{font-family:"Fira Code"}.fonts-module .montserrat{font-family:"Montserrat"}.fontsize-module{color:black}.fontsize-module .headings{display:flex;justify-content:space-around;align-items:center;flex-direction:column}

.form-module{background-color:#91a18d}.devise-form{display:grid;border-radius:10px;grid-gap:15px;height:auto;width:100%;min-width:30%;margin:auto;color:#282828;background-color:#91a18d;padding:20px 10px}.devise-form h2{text-align:center;font-size:2rem;min-width:80%;margin-left:auto;margin-right:auto;border-bottom:3px solid black}.devise-form .field{justify-self:center;width:70%}.devise-form .field label{font-family:"Montserrat";font-size:1.2rem}.devise-form .field input{outline:none;border:2px solid black;border-radius:5px;font-family:"Fira Code";letter-spacing:2px;font-weight:bold;font-size:1.2rem;width:100%;padding:10px}.devise-form .field input[type="checkbox"]{width:auto;margin-left:30px}.devise-form .actions{display:flex;justify-content:space-between;align-items:center;flex-direction:column}.devise-form .actions a{color:#e6c973;color:black;font-size:1.2rem;font-family:"Montserrat";margin-top:10px}.devise-form .actions p{font-size:1.25rem;margin-top:10px}.content-form{width:auto;max-width:100%;height:100%;display:grid;padding:2rem 0;grid-template-columns:35% 32.5% 32.5%;grid-template-rows:20% 40% 15% 10%;grid-row-gap:5%;grid-template-areas:"post-title post-file post-file" "post-content-text post-content-text post-content-text" "post-tags post-tags post-tags" "post-submit post-submit post-submit"}.content-form label{color:#282828;font-size:1.3rem}.content-form .field_title{align-self:center;display:flex;flex-flow:column nowrap;justify-self:left;grid-area:post-title;height:100%;width:75%;justify-content:center}.content-form .field_title #post_title{background-color:#c4c4c4;border:solid 0.25rem #c4c4c4;border-radius:0.25rem;height:35%;width:100%;margin-top:0.25rem;-webkit-filter:drop-shadow(0 0.25rem 0.25em #282828);filter:drop-shadow(0 0.25rem 0.25em #282828)}.content-form .field_content_text{grid-area:post-content-text;display:flex;flex-flow:column nowrap;height:100%;width:100%;justify-content:center}.content-form .field_content_text #post_content_text{background-color:#c4c4c4;border:solid 0.25rem #c4c4c4;border-radius:0.25rem;height:75%;width:100%;margin-top:0.25rem;-webkit-filter:drop-shadow(0 0.25rem 0.25rem #282828);filter:drop-shadow(0 0.25rem 0.25rem #282828)}.content-form .field_content_tags{grid-area:post-tags;display:flex;flex-flow:column nowrap;height:100%;width:100%;justify-content:center}.content-form .field_content_tags #post_tag_list{background-color:#c4c4c4;border:solid 0.25rem #c4c4c4;border-radius:0.25rem;height:75%;width:100%;margin-top:0.25rem;-webkit-filter:drop-shadow(0 0.25rem 0.25rem #282828);filter:drop-shadow(0 0.25rem 0.25rem #282828)}.content-form .content{grid-area:post-file;display:flex;flex-flow:row wrap;justify-content:end}.content-form .content label{padding-right:1rem}@media (max-width: 575.98px){.content-form .content label{padding-right:0.5rem;margin-left:0;font-size:90%;align-self:center}}.content-form .content .field_content_image{display:flex;justify-content:center}@media (max-width: 991.98px){.content-form .content .field_content_image{overflow:hidden;white-space:nowrap;text-overflow:ellipsis;margin-left:3rem}}@media (max-width: 575.98px){.content-form .content .field_content_image{overflow:hidden;white-space:nowrap;text-overflow:ellipsis;margin-left:0}}.content-form .content .field_content_image input{cursor:pointer;margin-top:0.25rem;-webkit-filter:drop-shadow(0 0.25rem 0.25rem #282828);filter:drop-shadow(0 0.25rem 0.25rem #282828)}.content-form .content .field_content_file{display:flex;justify-content:center}@media (max-width: 991.98px){.content-form .content .field_content_file{overflow:hidden;white-space:nowrap;text-overflow:ellipsis;margin-left:3.75rem}}@media (max-width: 575.98px){.content-form .content .field_content_file{overflow:hidden;white-space:nowrap;text-overflow:ellipsis;margin-left:0.75rem}}.content-form .content .field_content_file input{cursor:pointer;margin-top:0.25rem;-webkit-filter:drop-shadow(0 0.25rem 0.25rem #282828);filter:drop-shadow(0 0.25rem 0.25rem #282828)}.content-form .actions{grid-area:post-submit;margin:auto}

.tag{cursor:pointer;font-family:"Montserrat";padding:0.3rem 1.5rem;border:solid 2px #282828;border-radius:0.25rem;display:flex;justify-content:center;align-items:center;margin:0.5rem;background-color:#c4c4c4;-webkit-filter:drop-shadow(0 0.2rem 0.15rem #282828);filter:drop-shadow(0 0.2rem 0.15rem #282828);text-decoration:none;color:black;transition:ease-in-out 0.25s;font-size:1.3rem}.tag p{text-align:center}.tag:hover{border-color:#e6c973;-webkit-filter:drop-shadow(0 0.2rem 0.15rem #e6c973);filter:drop-shadow(0 0.2rem 0.15rem #e6c973);background-color:#282828;color:white;transform:scale(1.1)}.tag:hover img{-webkit-filter:invert(1);filter:invert(1)}.owned{background-color:#e6c973;transition:none}.owned:hover{border:solid 2px #282828;background-color:#e6c973;color:#282828;-webkit-filter:drop-shadow(0 0.2rem 0.15rem #282828);filter:drop-shadow(0 0.2rem 0.15rem #282828)}.submit{cursor:pointer;-webkit-filter:drop-shadow(0 0.15rem 0.25rem #282828);filter:drop-shadow(0 0.15rem 0.25rem #282828);text-transform:uppercase;padding:0.5rem 2rem;background-color:#e6c973;border:solid 0.15rem #282828;border-radius:0.25rem;font-family:"Montserrat";font-size:1.5rem;transition:transform 0.5s ease-in-out}.submit.small{font-size:1.25rem;padding:0.3rem 1.5rem}.submit.extra-small{font-size:1.25rem;padding:0.1rem 1.3rem}.submit:focus{border:solid 0.25rem #e6c973;background-color:#282828;color:white;-webkit-filter:drop-shadow(0 0.15rem 0.25rem #e6c973);filter:drop-shadow(0 0.15rem 0.25rem #e6c973)}.submit:hover{transform:scale(1.07)}.tag-small{width:auto;align-self:center;margin:0.5rem;background-color:white;padding:0.25rem 0.5rem 0.25rem 0.5rem;border-radius:0.35rem;outline:none;border:solid 1px black}

